import React, { useEffect, useCallback, useState } from 'react';
import { Form, Input, message, Row, Col, Typography, Button } from 'antd';
import { useNavigate } from 'react-router-dom';
import { RollbackOutlined } from '@ant-design/icons';
import { Upload, Map } from '../components';
import { getBase64 } from '../components/Upload';
import { currentUserUpdate, currentUser, location } from '../services';

const { Title } = Typography;

export default function Profile () {
  const navigate = useNavigate();
  const [form] = Form.useForm();
  const [position, setPosition] = useState({
    lat: 37.7749,
    lng: -122.4194
  });
  const getCurrentUser = useCallback(async () => {
    const userInfo = await currentUser();
    if (userInfo.id) {
      form.setFieldsValue({
        username: userInfo.username,
        avatar: userInfo.avatar
          ? [
              {
                uid: '-1',
                name: 'avatar',
                url: userInfo.avatar,
              },
            ]
          : undefined,
      })
    } else {
      navigate('/login')
    }
  }, [])

  const onFinish = async () => {
    form.validateFields().then(async values => {
      const result = {}
      const { avatar, username } = values
      if (avatar instanceof Array && avatar.length > 0) {
        result.avatar = /base64/.test(avatar[0].url) ? avatar[0].url : await getBase64(avatar[0].originFileObj)
      }
      result.username = username;
      const { message: text } = await currentUserUpdate(result);
      if (text) {
        message.success(`Edit UserInfo ${text}!`)
        getCurrentUser();
      }
    })
  }

  const getLocation = async () => {
    const data = await location();
    if (data && data.loc) {
      const [lat, lng] = data.loc.split(',');
      setPosition({
        lat,
        lng
      })
    }
  }

  useEffect(() => {
    getCurrentUser();
  }, [getCurrentUser]);

  useEffect(() => {
    getLocation();
  }, []);

  return (
        <Row align='middle' justify='center' style={{ minHeight: '400px', margin: 24 }}>
            <Col span={24}>
            <RollbackOutlined style={{ cursor: 'pointer', color: '#51288c', float: 'left', fontSize: 24 }} onClick={() => navigate('/dashboard')}/>
            </Col>
            <Col span={24} style={{ textAlign: 'center' }}>
                <Title>Edit Profile</Title>
            </Col>
            <Col span={24}>
                <Form
                    form={form}
                    autoComplete="off"
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 8 }}
                >
                    <Form.Item
                        label="UserName"
                        name="username"
                        rules={[{ required: true, message: 'Please input your userName!' }]}>
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="avatar"
                        name="avatar"
                        valuePropName='fileList'
                        rules={[{ required: true, message: 'Please upload your avatar!' }]}>
                        <Upload
                            maxCount={1}
                            listType="picture-card"
                        />
                    </Form.Item>
                    <Form.Item wrapperCol={{ offset: 8, span: 8 }} onClick={onFinish}>
                        <Button type="primary" block>
                            Submit
                        </Button>
                    </Form.Item>
                </Form>
            </Col>
            <Col span={8} style={{ minHeight: 400 }}>
                <Map position={position}/>
            </Col>
        </Row>
  )
}
